import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ITab } from 'src/app/shared/components/tab/tab.interface';

interface IDepartmentTab {
  first: {
    active: ITab,
    items: ITab[]
  };
  second: {
    active: ITab,
    items: ITab[]
  };
}

@Component({
  selector: 'app-department-view',
  templateUrl: './department-view.component.html',
  styleUrls: ['./department-view.component.scss']
})
export class DepartmentViewComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  public tabs: IDepartmentTab = {
    first: {
      active: null,
      items: [
        {id: 'roles', label: 'Roles'},
        {id: 'staffs', label: 'Staffs'},
      ]
    },
    second: {
      active: null,
      items: [
        {id: 'levels', label: 'Levels'},
        {id: 'channel', label: 'Channel'},
        {id: 'center', label: 'Center/Area'},
        {id: 'team', label: 'Team/Shop'}
      ]
    }
  };

  constructor(
  ) { }

  ngOnInit(): void {
    this.tabs.first.active = this.tabs.first.items[0];
    this.tabs.second.active = this.tabs.second.items[0];
  }

  tabSelect(tab: ITab, obj: string): void{
    this.tabs[obj].active = tab;
  }
}
