import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

export interface ITeamShop {
  id?: string;
  name: string;
  description: string;
}

@Component({
  selector: 'app-department-team-shop',
  templateUrl: './department-team-shop.component.html',
  styleUrls: ['./department-team-shop.component.scss']
})
export class DepartmentTeamShopComponent implements OnInit {

    public modalTitle: string;
    public form: FormGroup;
    public showModal = false;

    teams: ITeamShop[] = [
      {
        id: '1',
        name: 'Manager',
        description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
      },
      {
        id: '2',
        name: 'FrontLine',
        description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
      },
    ];
    constructor(
      private formBuilder: FormBuilder
    ) { }

    ngOnInit(): void {
      this.buildForm();
    }

    private buildForm(): void {
      this.form = this.formBuilder.group({
        id: '',
        name: ['', [Validators.required]],
        description: '',
      });
    }

    showAppForm(type: string, fn?: ITeamShop): void {
      if (type === 'add') {
        this.modalTitle = 'New Team/Shop';
      } else {
        this.modalTitle = `Edit ${fn.name}`;
        this.form.setValue(fn);
      }

      this.showModal = true;
    }

    closeModal(): void {
      this.form.reset();
      this.showModal = false;
    }


    submitForm(): void{
      this.form.markAllAsTouched();
      if (this.form.invalid) {
        return;
      }
      this.showModal = false;
      const {id, name, description }: ITeamShop = this.form.value;

      if (id) {
        this.teams.map( x => {
          if (id === x.id) {
            x.name = name;
            x.description = description;
          }
        });
      } else {
        this.teams.push({
          id: new Date().getTime().toString(),
          name,
          description
        });
      }

      this.form.reset();
    }

    deleteApp(role: ITeamShop): void {
      const sure = confirm('Are you sure?');
      if (sure) {
        this.teams = this.teams.filter( x => x.id !== role.id);
      }
    }
}



