import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { OrganizationComponent } from './organization.component';
import { SharedModule } from '../../shared/shared.module';
import { OrganizationViewComponent } from './organization-view/organization-view.component';
import { NodeViewComponent } from './node-view/node-view.component';
import { DepartmentViewComponent } from './department-view/department-view.component';
import { DepartmentRolesComponent } from './department-roles/department-roles.component';
import { DepartmentChannelsComponent } from './department-channels/department-channels.component';
import { DepartmentCenterAreaComponent } from './department-center-area/department-center-area.component';
import { DepartmentTeamShopComponent } from './department-team-shop/department-team-shop.component';
import { StaffModule } from '../staff/staff.module';
import { StaffViewComponent } from '../staff/staff-view/staff-view.component';
import { DepartmentLevelsComponent } from './department-levels/department-levels.component';

const routes: Routes = [
  { path: '', component: OrganizationComponent },
  { path: ':id', component: OrganizationViewComponent },
  { path: ':id/node', component: NodeViewComponent },
  { path: ':id/node/department/:departmentid', component: DepartmentViewComponent },
  { path: ':id/node/department/:departmentid/staff/:staffid', component: StaffViewComponent }
];

@NgModule({
  declarations: [
    OrganizationComponent,
    OrganizationViewComponent,
    NodeViewComponent,
    DepartmentViewComponent,
    DepartmentRolesComponent,
    DepartmentChannelsComponent,
    DepartmentCenterAreaComponent,
    DepartmentTeamShopComponent,
    DepartmentLevelsComponent
  ],
  imports: [
    CommonModule,
    SharedModule,
    ReactiveFormsModule,
    StaffModule,
    RouterModule.forChild(routes)
  ]
})
export class OrganizationModule { }
